.menu {
  box-sizing: border-box;
  height: 100vh;
  padding: 24px 0;
  overflow-y: auto;
  background-color: #0075ff;

  &__top {
    margin-bottom: 48px;
  }

  &__list {
    list-style: none;
    margin: 0 0 48px;
    padding: 0;
  }

  &__item:not(:last-child) {
    margin-bottom: 24px;
  }

  &__link {
    display: inline-block;
    padding-bottom: 8px;
    border-bottom: 1px solid transparent;

    color: #fff;
    text-decoration: none;
    font-weight: bold;
    font-size: 22px;
    line-height: 22px;
    letter-spacing: 2px;
    text-transform: uppercase;
    transition: background-color 0.3s;

    &:hover {
      border-color: #fff;
    }
  }

  &__call {
    display: block;
    width: max-content;

    font-weight: bold;
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 2px;
    text-transform: uppercase;
    text-decoration: none;
    color: #fff;

    &--phone {
      margin-bottom: 16px;
    }

    &--action {
      padding-bottom: 8px;
      border-bottom: 1px solid #fff;
    }
  }
}
